.page {
  height: 100%;
}

.currently-playing {
  position: absolute;
  width: 100%;
  text-align: center;
  font-weight: bold;
  border-bottom: 1px solid white;
  padding: 5px;
  box-sizing: border-box;
}

.page__content {
  height: 100%;
}

.page__content__wrapper {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  height: 100%;
  padding-top: 30px;
  box-sizing: border-box;
}

.page__content__wrapper.list-view {
  padding-top: 0px;
}

.wrapper__item {
  flex: 0 0 10%;
  margin: 0px auto;
}

.list__container {
  flex: 0 0 70%;
  width: 100%;
  height: 70%;
  box-sizing: border-box;
  border-radius: 20px;
  left: 0px;
}

.list__wrapper {
  box-sizing: border-box;
  /* border: 3px solid white; */
  overflow: auto;
  height: 90%;
}

.list__wrapper > div {
  font-weight: bold;
  letter-spacing: 0.3px;
  padding: 10px;
}

.list__wrapper > div:nth-child(odd) {
  background-color: white;
  color: black;
}

.list__wrapper > div:nth-child(even) {
}

.list-view {
  top: 0px;
  height: 100%;
  flex: 0 0 90%;
}
