html,
body,
#root {
  height: 100%;
  background-color: black;
  color: white;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  border: none;
  border-bottom: 3px solid white;
  background: black;
  color: white;
  font-size: 24px;
  text-align: center;
  padding: 10px;
  border-radius: 20px;
  max-width: 300px;
}

button {
  background: white;
  font-size: 20px;
  border: none;
  border-radius: 50px;
  width: 120px;
  display: flex;
  align-content: center;
  justify-content: center;
  font-weight: bold;
  padding: 10px;
}

.button--inverse {
  background-color: black;
  color: white;
}

.button__container {
  display: flex;
  justify-content: space-around;
}

.button__wrapper {
  margin: 30px;
}

.button__list-view {
  position: fixed;
  bottom: 10px;
  left: 6px;
  width: unset;
  font-size: 14px;
  height: unset;
}

.button__clear-view {
  position: fixed;
  bottom: 10px;
  right: 6px;
  width: unset;
  font-size: 14px;
  height: unset;
}
