#modal {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.modal__wrapper {
  width: 80%;
  background-color: white;
  color: black;
  padding: 20px;
  border-radius: 30px;
  font-size: 30px;
  text-align: center;
  box-shadow: #2c2c2c 11px 18px;
}

.modal__song {
  margin: 10px;
  margin-bottom: 20px;
  margin-top: 0px;
}

.modal__wrapper > button {
  background-color: black !important;
  color: white;
}
